@import '../helpers/_helpers';

.title {}

.box2 {
	margin: 0 0 0 50px;
	overflow: auto;
	border: 5px solid rgba(0, 0, 0, 0.4);
	width: 200px;
}


.position {
	width: 160px;
	height: 100px;
	cursor: pointer;

	&:before {
		position: absolute;
		content: 'frontend-developer html-верстальщик web-developer billiard player billiard coach';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #f4f4f4;
		padding: 17px;
		z-index: 10;
		opacity: 0;
		visibility: hidden;
		transition: all 0.3s ease;
	}
	&:hover:before {
		opacity: 1;
		visibility: visible;
	}
}
.photo {
    position: absolute;
    top: 40px;
    right: 40px;
    width: 110px;
    height: 140px;
    border-radius: 10px;
    background: url("../img/IMG_1-47.jpg") no-repeat center center;
    background-size: auto auto;
    background-size: 100%;
    transition: all 0.5s;
    border: 4px solid rgba(83,55,122,1);
}
@media screen and (max-width: 420px) {
    .photo {
        top: 20px;
        right: 20px;
    }
}
